// layout
.main-wrapper-section {
  height: 100vh;
  overflow: hidden;
}

.mobile-wrapper-section {
  border: 1px solid #cccccc;
  height: 600px;
  max-height: 600px;
  min-height: 600px;
  width: 100%;
  border-radius: 4px;
  overflow: hidden;

  &.scroll {
    overflow: auto;
    padding: 5px;
    max-height: 600px;
    height: 600px;
  }
}

.right-section-wrapper {
  border: 1px solid #cccccc;
  height: 600px;
  width: 100%;
  border-radius: 4px;
  overflow: auto;
  padding: 10px;
}

.border-box-section {
  height: 150px;
  width: 150px;
  border: 1px dashed #cccccc;
}


  // ================== component ==============

.service-card{
  width:400px;
&>div{
  display: flex;
  flex-direction: column;
  align-self: center;
  text-align: center;
  min-height: 79px;
  padding: 5px;
  pointer-events: none;
  }

  .service-icons-wrapper{
    padding: 6px 9px;
    width: auto;
    margin: 0 auto;
    background: #ccc;
    border-radius: 5px;
  }

}


// =======================Banner===================

.banner-img-pc{
  height: 250px;
  width:300px;
  
  &>img{
      object-fit: cover;
      height: 100%;
      width:100% ;
  }

}
