.flex-with-center{
    display: flex;
    align-items:center;
    justify-content: center;
}
.cursor-pointer{
    cursor: pointer;
}

.red-border{
    border-bottom: 0.5px dashed red !important;
    border-left: 0.5px dashed red !important;
    border-right: 0.5px dashed red !important;
    border-top: 0.5px dashed red !important;
}

.no-pointer-event{
    pointer-events: none;
}

.font-sm{
    font-size:12px;
}