.mobile-card{
    border:1px solid #cccc;
    height: 100px;
    width:100%;
    margin-top:5px; 
}

.mobile-double-card{
    border:1px solid #cccc;
    height: 100px;
    width:100%;
    margin-top:5px; 
    display: flex;
    
}

.card-right-section {
    height: 100px;
    width: 250px;
  }

  
.double-card-right-section{
    display: flex;
    height: 100px;
    width: 250px;
}

.add-new-card{
    border:1px solid #cccc;
    height: 100px;
    width:100%;
    margin-top:5px; 
    display: flex;
    align-items:center;
    justify-content: center; 
}

.banner-img-mobile{
    height: auto;
    width: 100%;
    padding:1px;

    &>img{
        height: auto;
        width:auto ;
    }
} 
.service-card-mobile{
    width: 298px;
}   